.TodoLine{
    display: -ms-flexbox;
    display: flex;
    width: 96%;
    background-color: #FFF;
    padding:10px;
    margin-left: 1.2em;
    color: #757575;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
    border-bottom: 1px dashed #d9d9d9;
 }

 .TodoLine:hover{
    background-color: #eeeeee;
}
 .TodoLine>.title{
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 0.31em 0 0 1.5em;
 }

input[type=checkbox]{
    width:20px; height:20px;
    margin-top: 4px;
}


 button{
    outline: none;
    border:none;
    display: inline-block;
    padding: 0 8px;
    text-align: center;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 100;
}

ol{
    list-style: none;
    margin: 0;
    padding: 0;
}


